import React from 'react';
import {Helmet} from 'react-helmet';

import favicon from '../images/plus.png';

import Footer from './Footer';
import Header from './Header';

export default ({title, description, children}) => (
  <>
    <Helmet>
      <title>{title || 'Elite Alliance'}</title>
      <meta
        name="description"
        content={description || 'Elite Alliance is an exclusive, invite-only MCN that works with some of the biggest and most recognizable names on YouTube.'}
      />
      <link
        rel="icon"
        type="image/png"
        href={favicon}
        sizes="16x16"
      />
    </Helmet>

    <Header />
    {children}
    <Footer />
  </>
);
